.form {
    width: 304px;
    padding: 50px 15px;
    margin: 0 auto;
    background-color: #fff;
    box-shadow: 0 9px 57px #b3b9c740;
    border-radius: 25px;
    display: flex;
    flex-flow: column;
}

.wrapper {
    position: relative;
}

.wrapper::after {
    position: absolute;
    content: '';
    height: 70px;
    width: 90px;
    left: 25px;
    background-image: url('../../assets/images/authForm_bg320.png');
    background-repeat: no-repeat;
}

.form_titleG {
    text-align: center;
    color: var(--text-dark);
    font-size: 11px;
    line-height: 17px;
    font-weight: 500;
}

.form_title {
    color: var(--text-dark);
    font-size: 11px;
    line-height: 17px;
    font-weight: 500;
}

.form_inputArea {
    padding-top: 25px;
    width: 270px;
}

.form_googleLink {
    width: 122px;
    height: 40px;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    margin: 25px auto 45px auto;
    box-shadow: 1px 2px 3px rgba(170, 178, 197, 0.29);
    border-radius: 20px;
    border: 2px solid #f5f7fa;
    background-color: #f5f7fa;
    font-weight: 500;
    line-height: 40px;
    color: #000000;
    font-size: 14px;
    text-decoration: none;
    transition: 0.2s ease-in-out;
}

.form_googleLink:hover {
  background-color: #fd812d;
  border: none;
  color: #fff;
  transition: 0.2s ease-in-out;
}

.form_googleLogo {
    width: 24px;
    height: 24px;
    margin: 0 5px 0 0;
}

.form_inputDescription {
    font-size: 11px;
    font-weight: 500;
    line-height: 35px;
}

.form_input {
    width: 270px;
    height: 48px;
    border-radius: 24px;
    border: 2px solid #f4f6fb;
    background-color: #f5f7fa;
    padding: 0 0 0 27px;
}

.form_buttons {
    width: 270px;
    padding-top: 40px;
    display: flex;
    justify-content: space-between;
}

.form_button {
    width: 125px;
    height: 45px;
    box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.1);
    border: none;
    border-radius: 17px;
    background-color: #f4f6fb;
    text-align: center;
    color: var(--text-dark);
    font-size: 11px;
    font-weight: 700;
    line-height: 45px;
    text-transform: uppercase;
    transition: 0.2s ease-in-out;
}

.form_button:hover {
  background-color: #fd812d;
  color: #fff;
  transition: 0.2s ease-in-out;
}

.form_button__active {
    width: 125px;
    height: 45px;
    box-shadow: 2px 3px 5px rgba(0, 0, 0, 0.1);
    border: none;
    font-size: 11px;
    text-transform: uppercase;
    border-radius: 17px;
    background-color: #f4f6fb;
    text-align: center;
    color: #ffffff;
    background-color: var(--orange);
    box-shadow: 2px 3px 5px rgba(255, 107, 8, 0.34);
}


@media screen and (min-width: 768px) {
    .wrapper::after {
        width: 170px;
        height: 222px;
        background-image: url('../../assets/images/authForm_bg768.png');
        left: auto;
        right: 0;
        bottom: 115px;
    }
    .form {
      display: flex;
      justify-content: center;
      align-items: center;
        margin-bottom: 180px;
        width: 420px;
        padding: 50px 70px;
        background-color: #fff;
    }
    .form_titleG {
        text-align: left;
        font-size: 13px;
    }
    .form_title {
        font-size: 13px;
    }
}

@media screen and (min-width: 1280px) {
    .wrapper {
        padding: 0;
        margin: 115px auto 0 auto;
    }
    .wrapper::after {
        display: none;
    }
}
